.form ::placeholder {
	color: var(--color-light-text);
}

.form :disabled,
.form :hover:disabled,
.form :focus:disabled,
.form :active:disabled {
	background-color: var(--bg-body);
	border-color: var(--bg-light);
	box-shadow: none;
	opacity: 0.7;
}

.form__fieldset {
	margin: 0;
	padding: 0;
	border: 0;
}

.form__legend {
	font-weight: 700;
	font-size: 22px;
	line-height: 45px;
	font-family: var(--font-bebas);
	color: var(--color-light-text);
	letter-spacing: 3px;
}

.form__grid {

	@media (min-width: @min-md) {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
		gap: 0 20px;
	}
}

.form__grid .form__label {
	grid-row-end: 2;
	grid-row-start: 1;
}

.form__label {
	display: block;
	font-weight: 500;
	font-size: 13px;
	line-height: 20px;
	color: var(--color-dark-text);
}

.form__control {
	box-sizing: border-box;
	width: 100%;
	height: 50px;
	margin-bottom: 24px;
	padding: 16px 20px;
	font-weight: 900;
	font-size: 14px;
	line-height: 1;
	color: var(--color-dark-text);
	letter-spacing: 0.5px;
	background-color: var(--bg-light);
	border: 0;
	border: 1px solid var(--bg-light);
	border-radius: 6px;
	transition: box-shadow 0.2s ease-in-out;

	@media (min-width: @min-sm) {
		font-size: 16px;
	}

	&:hover {
		box-shadow: 0px 4px 20px 0px var(--color-light-text);
	}

	&:focus-visible {
		outline: none;
		box-shadow: 0px 4px 20px 0px var(--color-light-text);
	}

	&:active {
		outline-color: transparent;
	}
}

.form__radio-wrap {
	display: flex;
	margin-bottom: 24px;
}

.form__radio-label {
	flex-grow: 1;
	box-sizing: border-box;
	padding: 17px 11px;
	font-weight: 900;
	font-size: 14px;
	line-height: 1;
	text-align: center;
	color: var(--color-light-text);
	border: 1px solid var(--bg-light);
	transition: box-shadow 0.2s ease-in-out;

	&:hover {
		box-shadow: 0px 4px 20px 0px var(--color-light-text);
	}

	&:first-of-type {
		border-radius: 8px 0 0 8px;
	}

	&:last-of-type {
		border-radius: 0 8px 8px 0;
	}
}

.form__radio {
	appearance: none;
}

.form__radio:disabled + .form__radio-label {
	opacity: 0.7;
}

.form__radio:disabled + .form__radio-label:hover {
	box-shadow: 0 0 0 0 transparent;
}

.form__radio:focus-visible + .form__radio-label {
	position: relative;
	box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__radio:checked + label {
	color: var(--color-dark-text);
	border-color: var(--bg-dark);
}

select.form__control {
	background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='18' height='11' viewBox='0 0 18 11'%3e%3cpath fill='%23d2bea6' fill-rule='evenodd' d='M9 11.01 0 3.07 2.54-.01 9 5.7 15.46 0 18 3.07Z'/%3e%3c/svg%3e ");
	background-repeat: no-repeat;
	background-position: right 10px center;
	appearance: none;
}

.form__select-icon {
	position: relative;
}

.form__select-icon select {
	padding-right: 40px;
	// padding-left: 60px;
	text-indent: 40px;
}

.form__select-icon option {
	text-indent: 0;
	padding-top: 16px;
	padding-bottom: 16px;
}

.form__select-icon::before {
	content: '';
	position: absolute;
	top: 13px;
	left: 16px;
	width: 30px;
	height: 24px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}

