.form__password {
	position: relative;

	& input {
		padding-right: 50px;
	}
}

.form__password:focus-within input,
.form__password-toggle:hover ~ input {
	box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__password-toggle {
	position: absolute;
	right: 0;
	width: 50px;
	height: 50px;
	background-color: transparent;
	border: 0;
	box-shadow: none;
}

.form__password-toggle {
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		width: 24px;
		height: 24px;
		background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24.1 22'%3e%3cpath fill='%23d2bea6' d='m23.7 11.6-1.6-1.2c0 .1-4.6 6.6-10 6.6s-10-6.5-10-6.6L.5 11.6c.7 1 1.5 1.9 2.3 2.7L.7 16.4 2 17.8l2.2-2.2c.7.6 1.5 1.1 2.2 1.6L5.1 20l1.8.8 1.3-2.7c.9.4 1.9.7 2.9.8V22h2v-3.1c1-.1 1.9-.4 2.9-.8l1.3 2.7L19 20l-1.3-2.8c.8-.5 1.5-1 2.2-1.6l2.2 2.2 1.4-1.4-2.1-2.1c.8-.9 1.6-1.8 2.3-2.7z'/%3e%3c/svg%3e ");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		transform: translate(-50%, -50%);
	}

