.form__checkbox-wrap {
	& p {
		padding-left: 60px;
	}
}

.form__checkbox {
	outline: none;
}

.form__checkbox-label {
	display: flex;
	align-items: center;
	font-weight: 900;
	font-size: 14px;
	line-height: 20px;
	color: var(--color-dark-text);
}

.form__checkbox-view {
	position: relative;
	display: block;
	flex-shrink: 0;
	width: 50px;
	height: 50px;
	margin-right: 10px;
	background-color: var(--bg-dark);
	border-radius: 8px;
	transition: box-shadow 0.2s ease-in-out;

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		width: 18px;
		height: 12px;
		transform: translate(-50%, -50%);
	}
}

.form__checkbox-label:hover .form__checkbox-view,
.form__checkbox-view:hover {
	box-shadow: 0 4px 20px 0 var(--color-light-text);
}

.form__checkbox:checked + .form__checkbox-view::before {
	background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='19' height='13' viewBox='0 0 19 13'%3e%3cpath fill='%23d2bea6' fill-rule='evenodd' d='M7.4 13 0 6.6l2.6-3 4.8 4 9-7.6L19 3Z'/%3e%3c/svg%3e");
	background-size: contain;
}

.form__checkbox:focus-visible .form__checkbox-view {
	outline-width: 2px;
	outline-style: double;
	outline-color: #d1bda5;
	outline-offset: -1px;
	box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.form__checkbox:disabled + .form__checkbox-view {
	box-shadow: none;
	opacity: 0.6;
}

.form__notes {
	line-height: 20px;
}

.form__number-wrap {
	display: flex;
}

.form__number-wrap ::-webkit-outer-spin-button,
.form__number-wrap ::-webkit-inner-spin-button {
	-webkit-appearance: none;
}

.form__number-wrap input {
	appearance: textfield;
}

.form__button-minus,
.form__button-plus {
	position: relative;
	width: 70px;
}

.form__button-minus {
	border-radius: 6px 0 0 6px;

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 20px;
		height: 4px;
		background-color: var(--color-light-text);
	}
}

.form__button-plus {
	border-radius: 0 6px 6px 0;

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 20px;
		height: 4px;
		background-color: var(--color-light-text);
	}

	&::after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 4px;
		height: 20px;
		background-color: var(--color-light-text);
	}
}

.form__number {
	text-align: center;
	border-radius: 0;
	margin-left: 2px;
	margin-right: 2px;
}

.form__social {
	display: flex;

	& select {
		min-width: 215px;
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		margin-right: 2px;
	}

	& input {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
